@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap);

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
html{
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  width: 1.2em;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #050d22;
}
/* Header designing starts here */
.mainbarspace {
  width: 100%;
}

.headdiv {
  margin: 80px 0px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
header {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  min-height: 100vh;
  background-color: #050d22;
}
.headLeft {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  width: 65%;
}
.headLeft h1 {
  font-size: 1.8rem;
  letter-spacing: 4px;
  margin: 20px 80px;
}
.headLeft p {
  color: #8193b2;
  margin: 0px 80px;
  font-size: 1.3rem;
  letter-spacing: 2px;
  word-spacing: 2px;
  text-align: left;
}
.utils {
  width: 30vw;
  margin-right: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.utils .util {
  border: 2px solid #57606a;
  margin: 20px;
  width: 11vw;
  height: 11vw;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 20px;
  transition: all 0.2s;
  box-shadow: 0px 0px 20px #00cfca;
}
.utilLogo {
  width: 40%;
}
.utils .util:hover {
  cursor: pointer;
  background-color: cadetblue;
  box-shadow: 0px 0px 10px black;
}
.utilName {
  text-align: center;
  text-decoration: none;
  padding: 20px;
  font-size: 1.2rem;
  letter-spacing: 2px;
  color: white;
}

@media screen and (max-width: 1000px) and (min-width: 700px) {
  .headdiv {
    flex-direction: column;
  }
  .headLeft {
    width: 95%;
  }
  .utils {
    margin-top: 50px;
    width: 90vw;
    margin-right: 0px;
  }
}
@media screen and (max-width: 700px) and (min-width: 600px) {
  .headdiv {
    flex-direction: column;
  }
  .headLeft {
    width: 95%;
  }
