    font-size: 1rem;
    margin: 0px 40px;
  }
  .headLeft h1 {
    font-size: 1.2rem;
    margin: 20px 40px;
  }
  .utils {
    margin-top: 50px;
    width: 90vw;
    margin-right: 0px;
  }
  .utils .util {
    width: 13vw;
    height: 13vw;
  }
}
@media screen and (max-width: 600px) and (min-width: 400px) {
  .headdiv {
    flex-direction: column;
  }
