  margin: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px grey;
  background-color: cadetblue;
}
@media screen and (max-width: 800px) and (min-width: 600px) {
  .project {
    width: 60vw;
    height: 45vh;
  }
}
@media screen and (max-width: 600px) and (min-width: 400px) {
  .project {
    width: 80vw;
    height: 45vh;
  }
}
@media screen and (max-width: 400px) {
  .project {
    width: 80vw;
    height: 60vh;
  }
}

.projects {
  border-bottom: 2px solid grey;
  background-color: rgb(241, 241, 241);
  width: 100%;
  min-height: 50vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}


.worknav {
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 10px grey;
  text-align: center;
}
.worknav a {
  text-decoration: none;
  color: #050d22;
  display: inline-block;
  font-family: monospace;
  letter-spacing: 4px;
  font-size: 1.5rem;
}
.worknav img {
  margin: 0px 20px;
  width: 40px;
}
.achieve {
  background-color: #d5f3ff;
  width: 100%;
  display: flex;
}
.achievecards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
}

.workcard {
  position: relative;
  margin: 20px;
  width: 500px;
  height: 300px;
  padding: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 5px grey;
}
.cardpost {
  width: 100%;
  color: white;
  height: 60%;
  border-radius: 10px;
  background-color: #091921;
}
.carddate {
  font-size: 0.8rem;
  letter-spacing: 1px;
  width: 200px;
  padding: 10px 20px;
  color: wheat;
  background-color: navy;
  position: absolute;
  top: 0px;
  right: 0px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
.cardbody {
  letter-spacing: 2px;
  font-size: 0.95rem;
  padding: 10px;
}
.cardbody h2 {
  color: blue;
  font-family: monospace;
  margin: 10px 0px;
  font-size: 1.5rem;
}
.cardbody span {
  color: grey;
}

.utilBox {
  padding: 20px;
  width: 100%;
  min-height: 80vh;
  background-color: rgb(241, 241, 241);
  border-bottom: 2px solid grey;
}

.finance {
  position: relative;
  margin: auto;
  padding: 20px;
  color: white;
  border-radius: 10px;
  width: 50%;
  overflow: hidden;
  background-color: #050d22;
  letter-spacing: 2px;
  text-align: center;
  transition: all 0.5s;
}
.finform {
  padding: 20px;
  width: 94%;
  margin: 20px auto;
  margin-top: 30px;
  color: black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0px 0px 5px grey;
  background-color: white;
}
.finform input {
  width: 100px;
  border: none;
  outline: none;
  padding: 0px 5px;
  border-radius: 5px;
  height: 4vh;
  color: darkcyan;
  font-weight: 600;
  font-family: cursive;
}
.finform > div {
  margin: 20px 0px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.finicon {
  padding: 2px 5px;
  color: skyblue;
}
.fincont {
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}
.finform label {
  font-family: "Courier New", Courier, monospace;
  font-size: 1.1rem;
  color: green;
}
@media screen and (max-width: 1000px) and (min-width: 800px) {
  .finance {
    width: 70%;
  }
}
@media screen and (max-width: 800px) and (min-width: 600px) {
  .finance {
    width: 90%;
  }
}
@media screen and (max-width: 600px) and (min-width: 460px) {
  .finance {
    width: 100%;
  }
  .finform label {
    font-size: 0.9rem;
  }
  .finform input {
    width: 60px;
  }
}
@media screen and (max-width: 460px) {
  .finform > div {
    flex-direction: column;
  }
  .finance {
    width: 100%;
  }
  .finform label {
    font-size: 0.9rem;
  }
  .finform input {
    width: 60px;
  }
  .fincont {
    margin: auto;
    margin-top: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
}

