.project {
  position: relative;
  width: 40vw;
  height: 40vh;
  margin: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px grey;
  background-color: cadetblue;
}
@media screen and (max-width: 800px) and (min-width: 600px) {
  .project {
    width: 60vw;
    height: 45vh;
  }
}
@media screen and (max-width: 600px) and (min-width: 400px) {
  .project {
    width: 80vw;
    height: 45vh;
  }
}
@media screen and (max-width: 400px) {
  .project {
    width: 80vw;
    height: 60vh;
  }
}
