.mobilemainnav {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100vh;
  align-items: center;
  background-color: #050d22;
  z-index: 100;
  transition: all 0.5s;
}
.mobilelinks {
  margin-top: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: inherit;
}
.closebtn {
  text-align: right;
}
.closebtn i {
  margin: 20px;
  color: wheat;
  font-size: 1.5rem;
}
.moblink {
  color: white;
  text-decoration: none;
  margin: 20px;
}
.mobnavfoot {
  margin-top: 100px;
  letter-spacing: 2px;
  font-size: 1rem;
  font-family: monospace;
  padding-top: 20px;
  border-top: 2px solid grey;
  width: 100%;
  text-align: center;
  color: wheat;
}
